.page-not-found {
    padding: 20px;
    min-width: 360px;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;

    &__inner {
        display: flex;
        align-items: center;
    }

    &__desc {
        margin-left: 16px;
    }

    &__title {
        font-size: 20px;
        line-height: 1;
        font-weight: 700;
        margin-bottom: 24px;
    }

    &__text {
        margin-bottom: 24px;
    }

    &__emoji {
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url(data:image/png;base64,) no-repeat;
        -moz-background-size: contain;
        background-size: contain;
    }

    &__btn {
        a {
            cursor: pointer;
            color: #fff;
            display: inline-block;
            padding: 8px 12px;
            border-radius: 4px;
            background: #005BD1;
            text-transform: uppercase;
        }
    }

    @include sm {
        padding: 16px;
        
        &__inner {
            flex-wrap: wrap;
            justify-content: center;
        }

        &__pic {
            width: 100%;
            height: 204px;
            text-align: center;
            margin-bottom: 16px;

            img {
                max-height: 100%;
            }
        }

        &__desc {
            margin-left: 0;
        }

        &__title,
        &__text {
            margin-bottom: 24px;
        }
    }
}